<template>
  <div class="about">
    <Header />
    <div>
      <div v-if="about_content.length > 0">
        <img src="@/assets/images/b2.jpg" v-if="about_content[0].banner === null" alt="" class="img-resize img-fluid w-100 position-relative" style="height: 30em;" />
        <img :src="about_content[0].banner" v-else alt="" class="img-resize img-fluid w-100 position-relative" style="height: 30em;" />
      </div>
      <div v-else >
        <img src="@/assets/images/b2.jpg" alt="" class="img-resize img-fluid w-100 position-relative" style="height: 30em;" />
      </div>
      <div class="about-caption">
        <h1 class="text-shadow text-white display-1 about-caption-text">About Us</h1>
      </div>
    </div>

    <div class="container">
      <div class="row py-5">
        <div class="col-xl-8 col-12 text-start">
          <h3 class="font-weight-bold">About CACSAFPI</h3>
          <p>{{about_content.length > 0 ? about_content[0].first_content : ''}}</p>
        </div>
        <div class="col-xl-4 col-12" v-if="current_president.length > 0">
          <div class="card m-auto" style="width: 18rem;">
            <img src="@/assets/images/profile.png" v-if="current_president[0].user.profile_picture === null || current_president[0].user.profile_picture === ''" class="card-img-top img-contain" alt="President_img" height="200">
            <img :src="current_president[0].user.profile_picture" v-else class="card-img-top img-contain" alt="President_img" height="200">
            <div class="card-body">
              <h5 class="mb-0 font-weight-bold">{{`${current_president[0].user.first_name} ${current_president[0].user.last_name}`}}</h5>
              <small>{{`${current_president[0].office.post} ${current_president[0].generation.from}/${current_president[0].generation.to}`}}</small>
              
            </div>
          </div>
        </div>
        
      </div>

      <div class="pb-5" v-if="current_executives.length > 0">
        <h3 class="font-weight-bold text-start">THE {{`${current_president[0].generation.from}/${current_president[0].generation.to}`}} EXECUTIVES</h3>

        <VueSlickCarousel :arrows="true" :dots="true" v-bind="settings">
          
          <div class="p-2" v-for="executive in current_executives" :key="executive.id">
            <div class="card" style="width: 18rem;">
              <img src="@/assets/images/profile.png" v-if="executive.user.profile_picture === null || executive.user.profile_picture === ''" class="card-img-top img-contain" alt="President_img" height="200">
              <img :src="executive.user.profile_picture" v-else class="card-img-top img-contain" alt="img" height="200">
              <div class="card-body">
                <h5 class="mb-0 font-weight-bold text-truncate" :title="`${executive.user.first_name} ${executive.user.last_name}`">{{`${executive.user.first_name} ${executive.user.last_name}`}}</h5>
                <p class="mb-0 text-truncate" :title="`${executive.office.post} ${executive.generation.from}/${executive.generation.to}`">{{`${executive.office.post} ${executive.generation.from}/${executive.generation.to}`}}</p>
              </div>
            </div>
            
          </div>
          
        </VueSlickCarousel>
      </div>

    </div>

    <Footer />
  </div>
</template>


<script>

import Header from "../components/layouts/Header";
import Footer from "../components/layouts/Footer";

import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

import { mapGetters, mapActions } from "vuex";

export default {
  name: "About",
  data(){
    return {
      settings: {
        "dots": true,
        "infinite": false,
        "speed": 500,
        "slidesToShow": 4,
        "slidesToScroll": 4,
        "initialSlide": 0,
        "responsive": [
          {
            "breakpoint": 1024,
            "settings": {
              "slidesToShow": 3,
              "slidesToScroll": 3,
              "infinite": true,
              "dots": true
            }
          },
          {
            "breakpoint": 600,
            "settings": {
              "slidesToShow": 2,
              "slidesToScroll": 2,
              "initialSlide": 2
            }
          },
          {
            "breakpoint": 480,
            "settings": {
              "slidesToShow": 1,
              "slidesToScroll": 1
            }
          }
        ]
      },
      about_content: [],
      current_executives: [],
      current_president: []
    }
  },
  components: {
    Header, 
    Footer,
    VueSlickCarousel
  },
  computed: mapGetters(['contents', 'executives']),
  methods: {
    ...mapActions(['fetchContents', 'fetchExecutives'])
  },
  created(){
    this.fetchContents();
    this.fetchExecutives();
    this.about_content = this.contents.filter(content => content.page === 'about');

    this.current_executives = this.executives.filter(executive => parseInt(executive.generation.status) === 1 && executive.office.slug !== 'president');
    this.current_president = this.executives.filter(executive => parseInt(executive.generation.status) === 1 && executive.office.slug === 'president');
   
  }, 
}
</script>

<style>
  .about-caption{
    position: absolute;
    z-index: 100;
    top: 0%;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.575);
    height: 30em;
    padding-top: 12em;
  }

  .img-contain{
    object-fit: contain;
  }
  
</style>
